<div class="container"><form class="form__auth" @submit="formHandler" v-if="!getUserData.request"><div class="form__auth-wrapp"><span class="form__auth-header"><img class="form__auth-logo" src="@/assets/img/logo/micros.svg"><h3 class="form__auth-title">{{ msg }}</h3></span><div class="ui-form-row" v-if="fieldError"><div class="ui-form-content"><div class="ui-alert ui-alert-danger ui-alert-icon-danger"><span class="ui-alert-message"><strong>Введите корректные данные</strong></span><span class="ui-alert-close-btn" @click="setStatus"></span></div></div></div><div class="ui-form-row"><div class="ui-form-content"><div class="ui-ctl ui-ctl-textbox ui-ctl-w100" :class="{'ui-ctl-warning':fieldError}"><input class="ui-ctl-element" type="text" placeholder="Login" v-model="login" @input="setStatus"></div></div></div><div class="ui-form-row"><div class="ui-form-content"><div class="ui-ctl ui-ctl-textbox ui-ctl-w100" :class="{'ui-ctl-warning':fieldError}"><input class="ui-ctl-element" type="password" placeholder="Password" v-model="password" @input="setStatus"></div></div></div><div class="ui-form-row"><div class="ui-form-content"><button class="ui-btn ui-btn-round" :disabled="btnDisable" :class="{'ui-btn-primary':false}" type="submit" @click="checkstatus"> Авторизация</button></div></div></div></form><div class="messge_box" v-if="getUserData.request"><div class="ui-form-row"><div class="ui-form-content"> <pre>{{getUserData}}</pre></div></div></div></div>