import { createStore } from 'vuex'

export default createStore({
  state: {
    userData:{
      request:null
    }
  },
  getters: {
    getUserData(state){
      return state.userData
    }
  },
  mutations: {
    userDataMutation(state,userData){
      return state.userData = userData;
    }
  },
  actions: {
    async getAuth({commit},objectAuth){
      
      let login = objectAuth.login,
        password = objectAuth.password;
      let myHeaders = new Headers();
      myHeaders.append("Authorization", "Basic "+btoa(`${login}:${password}`));

      let requestOptions = {
          method: 'POST',
          headers: myHeaders,
          redirect: 'follow',
      };
      
      // commit("userDataMutation",{request:false});
      // commit("userDataMutation",{"request":true,data:{"ID":"8262"}});
      // return;
      fetch("https://micros.uz/it/solutions_our/micros.tech.support/ajax.php?AUTH=true", requestOptions)
      .then(response => response.text())
      .then(result => {
        console.log(JSON.parse(result));
        // if(JSON.parse(result).request){
          commit("userDataMutation",JSON.parse(result));
        // }else{
        //   commit("userDataMutation",false);
        // }
      })
      .catch(error => console.log('error', error));
      
    }
  },
  modules: {
  }
})
